import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import { debounceImmediate, getWindowSize, onWindowResize } from '../utils';

const marqueeBlogAnimation = ({ trackSelector, slideSelector } = {}) => {
	const $tracks = document.querySelectorAll(trackSelector);
	if (!$tracks.length) return null;

	gsap.registerPlugin(ScrollTrigger);
	ScrollTrigger.refresh();

	const defaultSpeed = 2;

	let speed = defaultSpeed;
	let tickers = [];

	const addTicker = (fn) => {
		gsap.ticker.add(fn, false);
	};

	const removeTicker = (fn) => {
		gsap.ticker.remove(fn);
	};

	const init = () => {
		$tracks.forEach(($track, index) => {
			const $slides = $track.querySelectorAll(slideSelector);
			const defaultOffset = $track?.dataset?.offset || 0;

			const changeSpeedOnScroll = !!$track.dataset.changeSpeed;

			if ($slides.length < 2) return;

			const trackWidth = $slides[0].clientWidth;

			let offset = defaultOffset;

			gsap.set($track, {
				x: offset,
			});

			tickers[index] = () => {
				if (offset <= -trackWidth) {
					offset = 0;
				} else if (offset > 0) {
					offset = -trackWidth;
				}

				gsap.set($track, { x: offset });
				offset -= speed;
			};

			ScrollTrigger.create({
				trigger: $track,
				start: 'top bottom',
				end: 'bottom top',
				onEnter: () => addTicker(tickers[index]),
				onEnterBack: () => addTicker(tickers[index]),
				onLeave: () => removeTicker(tickers[index]),
				onLeaveBack: () => removeTicker(tickers[index]),
			});
		});
	};

	let prevWindowWidth = null;

	onWindowResize(() => {
		const { windowWidth } = getWindowSize();

		if (windowWidth !== prevWindowWidth) {
			tickers.forEach((ticker) => {
				removeTicker(ticker);
			});
			init();
		}

		prevWindowWidth = windowWidth;
	});

	return null;
};

export default marqueeBlogAnimation;
